$prefix: pfe;

@import "theme";
@import "typography";

.PFElement {
    font-family: var(--#{$prefix}-theme--font-family);
    color: var(--#{$prefix}-broadcasted--color--text);
    a {
        color: var(--#{$prefix}-broadcasted--color--ui-link);
        @each $state in (visited, hover, focus) {
            &:#{$state} {
                color: var(--#{$prefix}-broadcasted--color--ui-link--#{$state});
            }
        }
    }
    
    > *:first-child {
        margin-top: 0;
    }
    h1, h2, h3, h4, h5, h6 {
        font-family: var(--#{$prefix}-theme--font-family--heading);
    }

    h1 {
        font-size:   var(--pfe-theme--font-size--alpha, 36px);
        line-height: var(--pfe-theme--line-height--alpha, 1.3);
        font-weight: var(--pfe-theme--font-weight--alpha, 400);
    }

    h2 {
        font-size:   var(--pfe-theme--font-size--beta, 28px);
        line-height: var(--pfe-theme--line-height--beta, 1.3);
        font-weight: var(--pfe-theme--font-weight--beta, 400);
    }

    h3 {
        font-size:   var(--pfe-theme--font-size--gamma, 24px);
        line-height: var(--pfe-theme--line-height--gamma, 1.3);
        font-weight: var(--pfe-theme--font-weight--gamma, 400);
    }

    h4 {
        font-size:   var(--pfe-theme--font-size--delta, 20px);
        line-height: var(--pfe-theme--line-height--delta, 1.5);
        font-weight: var(--pfe-theme--font-weight--delta, 400);
    }

    h5 {
        font-size:   var(--pfe-theme--font-size--epsilon, 18px);
        line-height: var(--pfe-theme--line-height--epsilon, 1.2);
        font-weight: var(--pfe-theme--font-weight--epsilon, 700);
    }

    h6 {
        font-size:   var(--pfe-theme--font-size--zeta, 16px);
        line-height: var(--pfe-theme--line-height--zeta, 1.2);
        font-weight: var(--pfe-theme--font-weight--zeta, 700);
    }
    pre, code {
        font-family: var(--#{$prefix}-theme--font-family--code);
    }
}

/* COMPONENT SPECIFIC STYLES */
#{$prefix}-cta[priority="primary"] a,
#{$prefix}-cta[priority="secondary"] a {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1em;
}

#{$prefix}-cta:not([priority="primary"]) a,
#{$prefix}-cta:not([priority="secondary"]) a {
    text-decoration: none;
    &:after {
        margin-bottom: .25em;
    }
}
